<template>
  <div class="swiper-container">
    <div class="swiper-wrapper">
      <div class="swiper-slide" v-for="(item,index) in adListData" :key="index">
        <img :src="item.img" />
      </div>
    </div>
    
    <div v-if="showBtn" @click="goWebView()" class="arrows-btn" style="z-index: 11;">
      <van-icon name="arrow" />
      <van-icon name="arrow" />
      <van-icon name="arrow" />
    </div>
    <!-- 如果需要分页器 -->
    <div class="swiper-pagination"></div>
  </div>
</template>

<script>
  import Swiper from 'swiper/dist/js/swiper.js';
  import 'swiper/dist/css/swiper.min.css';
  export default {
    props:{
      adListData:Array
    },
    data(){
      return {
        showBtn:false,
        canClick:true,
        index:0,
      }
    },
    mounted() {
      let _this = this;
      //初始化轮播控件
      let mySwiper = new Swiper('.swiper-container', {
        autoplay: {
          disableOnInteraction: false,
        }, //可选选项，自动滑动
        spaceBetween: 5,//间距
        centeredSlides: true,
        loop: true,
        loopedSlides: 8,
        direction: 'horizontal',
        slidesPerView: "auto",
        pagination: {
          el: '.swiper-pagination'
        },
        on:{
          click: function(e){
            if(!_this.canClick){
              _this.canClick = true;
              return
            }
            _this.$homePageJumps(_this.adListData[this.realIndex]);
          },
          slideChange: function(){
            _this.index = this.realIndex;
            if(_this.adListData[this.realIndex].extLink != ''){
              _this.showBtn = true;
            }else{
              _this.showBtn = false;
              _this.canClick = true;
            }
          },
        },
      })
    },
    methods:{
      goWebView(){
        this.canClick = false;
        this.$router.push({
          path: '/webView',
          query:{
            url:this.adListData[this.index].extLink,
            name:this.adListData[this.index].name
          }
        })
      }
    }
  }
</script>

<style scoped>
  .swiper-container {
    background-color: #fff;
  }

  .swiper-wrapper {
    width: 100%;
  }

  .swiper-slide {
    width: 95% !important;
  }

  .swiper-container img {
    width: 100%;
    height: 280px;
    display: block;
    border-radius: 16px;
  }
  .arrows-btn{
    position: absolute;
    color: #FFFFFF;
    bottom: 20px;
    right: 20px;
    height: 40px;
    border-radius: 10px;
    width: 120px;
    justify-content: center;
    background-color: rgba(0,0,0,0.3);
    display: flex;
    align-items: center;
  }
</style>
