<script>
  import {splitRoleID} from './../../utils/activity.js'
  export default {
    props:{
      flootListData:Array
    },
    data() {
      return {
        list: [],
        countDownList:{},//倒计时列表
        loading:true,//下拉加载
        finished:false,//可以下拉加载
        floorIndex:0,//加载楼层下标
        falg: 0,
      }
    },
    mounted() {
      this.appGetMallFloorProd();
    },
    activated() {
      this.finished = false
      this.list = []
      this.floorIndex = 0
      this.getInfo()
    },
    deactivated() {
      this.falg = this.floorIndex
    },
    methods: {
      onLoad(){//下拉加载
        this.appGetMallFloorProd();
      },
      async getInfo(){
        for(let i = 0; i < this.falg; i++){
          await this.appGetMallFloorProd(this.flootListData[i].code)
        }
      },
      appGetMallFloorProd(code){
        let params = {
          identity:code || this.flootListData[this.floorIndex].code,
          limit:10,
        }
        if(this.flootListData[this.floorIndex].option ==103){
          params.jsonStr=JSON.stringify({classid:this.flootListData[this.floorIndex].route})
        }
        if(this.flootListData[this.floorIndex].template ==204){
          params.jsonStr=JSON.stringify({brandno:this.flootListData[this.floorIndex].route})
        }
        JNB.query({
          serverName: "goodsServer",
          cls: "MainPageModule",
          method: "pageInfo",
          params:params,
          callback :(data)=>{
            this.loading = false;//加载完成
            data = JSON.parse(data);
            console.log(data)
            if(data.code == 200){
              if(data.data && data.data.list && data.data.list.length>0){
                if(data.data.list.length%2 != 0 && this.flootListData[this.floorIndex].template != 205){
                  data.data.list.pop();
                }
                if(data.data.list.length>0){
                  this.$request_img({//获取商品图片
                    context: this,
                    arr: data.data.list
                  })
                
                  data.data._item = this.flootListData[this.floorIndex];
                  if(data.data.actObj){
                    data.data._item.attr = {
                      actObj:data.data.actObj
                    }
                  }
                  this.list.push(data.data);
                  if(data.data._item.template == 202 ||data.data._item.template == 203){
                    //团购或秒杀类型，设置倒计时
                    this.secondKill(
                      this.$stringToDate(data.data._item.attr.actObj.now),
                      data.data._item.attr.actObj.edate,
                      this.list.length-1
                    )
                  }
                }
              }else if(this.flootListData[this.floorIndex].template == 205){
                let data = {
                  _item: this.flootListData[this.floorIndex]
                }
                this.list.push(data);
              }
              if(this.floorIndex == this.flootListData.length-1){
                this.finished = true;//锁定下拉加载
                return
              }
              this.floorIndex++
            }
          },
        })
      },
      // 设置倒计时
      secondKill(date,eDate,index){
        let endDate = this.$stringToDate(
          date.getFullYear() +
            "-" +
            (Number(date.getMonth()) + 1) +
            "-" +
            date.getDate() +
            " " +
            eDate
        );
        let times = Math.floor((endDate - date) / 1000);
        let _this = this;
        if (times >= 0) {
          let timer;
          this.$set(_this.countDownList, index , {
            h:0,
            m:0,
            s:0
          }) 
          timer = setInterval(function() {
            times--;
            _this.countDownList[index].h = Math.floor(times / 60 / 60);
            _this.countDownList[index].m = Math.floor(times / 60) % 60;
            _this.countDownList[index].s = times % 60;
            if (times <= 0) {
              clearInterval(timer);
            }
          }, 1000);
        }
      },
      //商品减少
      fn_minus(obj){
        this.$manageGoodNum(this,obj,-1);
      },
      //商品添加
      fn_add(obj){
        this.$manageGoodNum(this,obj,-2);
      },
      //输入商品数量
      fn_blur(obj){
        obj._showInput = false;
        if(obj.cart == ''){
          obj.cart = 0;
        }
        this.$manageGoodNum(this,obj,obj.cart);
      },
      //获取焦点时一直显示input框
      fn_focus(obj){
        obj._showInput = true;
      },
      //商品详情跳转
      toDetail(obj){
        this.$router.push({
          path: '/goodsDetail',
          query:{
            sku:obj.sku
          }
        })
      },
      
    },
  }
</script>

