<template>
  <div class="home" ref="home" id="home">
    <search></search>
    <banner-swiper
      v-if="allData && allData.ad && allData.ad.length > 0"
      :adListData="allData.ad"
    ></banner-swiper>
    <home-menu
      v-if="allData && allData.apptab && allData.apptab.length > 0"
      :apptabListData="allData.apptab"
    ></home-menu>
    <news
      v-if="allData && allData.ad && allData.ad.length > 0"
      :adListData="allData.ad"
    ></news>
    <banner-list
      v-if="allData && allData.sp && allData.sp.length > 0"
      :spListData="allData.sp"
    ></banner-list>
    <floor
      v-if="allData && allData.floot && allData.floot.length > 0"
      :flootListData="allData.floot"
    ></floor>
  </div>
</template>
<script>
import * as types from "@/store/mutations";
import { putCache } from "@/utils/cache";
import Search from "@/components/Search.vue";
import BannerSwiper from "@/components/home/BannerSwiper.vue";
import HomeMenu from "@/components/home/HomeMenu.vue";
import News from "@/components/home/News.vue";
import BannerList from "@/components/home/BannerList.vue";
import Floor from "@/components/home/Floor.vue";
import { setTimeout } from "timers";

export default {
  components: {
    Search,
    BannerSwiper,
    HomeMenu,
    News,
    BannerList,
    Floor
  },
  data() {
    return {
      allData: {},
      homeTop: 0
    };
  },
  computed: {
    userInfo() {
      return this.$loadUserInfo();
    }
  },
  created() {
    this.$store.commit(types.SET_TABBAR_INDEX, 0);
  },
  // mounted() {
  //   this.getHomeData();
  // },
  activated() {
    this.getHomeData();
    // this.$nextTick(() => {
    //   document.documentElement.scrollTop = this.homeTop || 0;
    // });
  },
  // beforeRouteLeave(to, from, next) {
  //   this.homeTop = document.documentElement.scrollTop || 0;
  //   next();
  // },
  methods: {
    getHomeData() {
      JNB.query({
        serverName: "goodsServer",
        cls: "MainPageModule",
        method: "pageInfo",
        callback: data => {
          data = JSON.parse(data);
          console.log(data)
          if (data.code == 200) {
            this.allData = data.data;
          }
        }
      });
    }
  }
};
</script>

<style scoped>
.home {
  background-color: #f5f5f5;
}
</style>
