<template>
  <div class="news" v-if="newsList.length>0">
    <div class="news_icon">
      <img src="../../assets/home/news.png" />
    </div>
    <div class="news_title">
      <van-swipe class="news_title_swipe" :autoplay="3000" vertical :show-indicators="false">
        <van-swipe-item class='news_title_txt' v-if="newsList.length == 0">暂无公告</van-swipe-item>
        <van-swipe-item 
          class='news_title_txt' 
          v-else v-for="(news,index) in newsList" 
          :key="index"
          @click="toNewsDetails(index)"
        >{{news.type}}{{news.title}}</van-swipe-item>
      </van-swipe>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        newsList: []
      }
    },
    mounted(){
      this.get_newsList();
    },
    methods:{
      get_newsList (){
        JNB.query({
          serverName: "globalServer",
          cls: "NoticeModule",
          method: "query",
          callback :(data) => {
            const dataStr = JSON.parse(data)
            if (dataStr.code === 200) {
              sessionStorage.setItem(
                "newsListAll",
                JSON.stringify(dataStr.data)
              );
              //this.newsList = dataStr.data.slice(0, 4);
              this.newsList = dataStr.data;
            }
          }
        })
      },
      // 新闻详情
      toNewsDetails(index) {
        this.$router.push({
          path: "/newsDetails",
          query: {
            index: index
          }
        });
      },
    }
  }
</script>

<style scoped>
  .news {
    background-color: #fff;
    height: 70px;
    padding: 20px;
    box-sizing: border-box;
    line-height: 30px;
    display: flex;
  }

  .news_icon img {
    width: 100px;
    height: 30px;
    display: block;
  }

  .news_title {
    height: 30px;
    margin-left: 26px;
    flex: 1;
    line-height: 30px;
    font-size: 28px;
    color: #0a0a0a;
    overflow: hidden;
  }

  .news_title_swipe {
    height: 30px;
    text-align: left;
  }

  .news_title_txt {
    height: 30px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
</style>
